@font-face {
  font-family: averta;
  src: url(./fonts/AvertaDemoPECuttedDemo-Regular.otf) format("otf"),
    url(./fonts/AvertaDemoPE-ExtraBold.otf) format(otf);
  font-display: optional;
  font-weight: 600;
}
.rsponsive-nav {
  display: none;
}
@font-face {
  font-family: __averta_Fallback_cd5f11;
  src: local("Arial");
  ascent-override: 99.76%;
  descent-override: 26.48%;
  line-gap-override: 0%;
  size-adjust: 104.35%;
}
body {
  font-family: averta, __averta_Fallback_cd5f11;
}
.logo {
  h1 {
    font-family: cursive;
    font-size: 1.5rem;
    margin-bottom: 0;
    -webkit-text-stroke: 1.9px #000;
    color: transparent;
    font-weight: 800;
    letter-spacing: 0.05em;
    text-shadow: none;
    text-transform: uppercase;
  }
}

nav {
  ul {
    li {
      padding-right: 3rem;
      list-style: none;
      a {
        text-decoration: none;
        color: black;
        font-family: averta, __averta_Fallback_cd5f11;
        font-weight: 600;
        line-height: 1;
        letter-spacing: 1px;
      }
    }
  }
}
.nav-btn {
  .btn {
    padding: 1rem 3.1rem;
    line-height: 1.5;
    text-align: center;
    max-width: 24.2rem;
    border-radius: 2px;
    color: #fff;
    background-color: #0755e9;
    border: 1px solid #0755e9;
    font-family: averta, __averta_Fallback_cd5f11;
    font-weight: 600;
    letter-spacing: 1px;
  }
}
.hero {
  padding-top: 12rem;

  .hero-section {
    h1 {
      max-width: 890px;
      margin: auto;
      -webkit-text-stroke: 1px #000;
      color: transparent;
      font-size: 58px;
      font-weight: 900;
      letter-spacing: 0.05em;
      line-height: 70px;
      text-shadow: none;
      text-transform: uppercase;
    }
    p {
      font-family: Poppins, sans-serif;
      text-align: center;
      color: #707070;
      letter-spacing: 0;
      font-weight: 600;

      span {
        color: #ff3c2e;
      }
    }
  }
  .heroSiblingSection {
    margin-top: 9rem;
    .col-sm-4 {
      text-align: center;
      h2 {
        font-family: averta, __averta_Fallback_cd5f11;
        font-weight: 600;
      }
      p {
        max-width: 12.5rem;
        color: #707070;
        font-weight: 600;
        margin: auto;
        letter-spacing: 0.53px;
        font-family: averta, __averta_Fallback_cd5f11;
      }
    }
  }
  .hero-image {
    margin-top: 9rem;
    margin-bottom: 4rem;
    .col-12 {
      .image {
        img {
          width: 100%;
        }
      }
    }
  }
}
.nav-item {
  a {
    font-weight: 600;
  }
}

.caseimg {
  img {
    width: 100%;
  }
}
.firstSection {
  h1 {
    font-weight: 600;
  }
  h2 {
    font-family: lato;
    font-style: normal;
    max-width: 890px;
    margin: auto;
    -webkit-text-stroke: 1.235px #000;
    color: transparent;
    font-size: 58px;
    font-weight: 900;
    letter-spacing: 0.05em;
    line-height: 70px;
    text-shadow: none;
    text-transform: uppercase;
  }
  p {
    font-family: lato;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.277778px;
    color: #8e8ea1;
  }
  button {
    display: flex;
    justify-content: center;
    align-items: center;
    /* flex: 0 1 236px; */
    padding: 3px 56px;
    border-radius: 12px;
    font-family: Poppins, sans-serif;
    font-size: inherit;
    line-height: 3em;
    border: 1px solid #0755e9;
    font-weight: 500;
    letter-spacing: 0.5px;
    color: #fff;
    margin-top: 1.9rem;
    background-color: #0755e9;
    transition: 0.3s ease-in;
    box-shadow: 0 15px 30px rgba(64, 64, 137, 0.2);
  }
  button:hover {
    background-color: white;
    border: 1px solid black;
    color: black;
  }
}
// portfolio-section

.portfolio-section {
  .row {
    .col-lg-6 {
      .pImg-2 {
        width: 85%;
      }
    }
  }
}

// Scroll Bar
.transition {
  transform: translate3d(-410px, 0px, 0px);
}
.cccc {
  overflow: hidden;
  padding-top: 2rem;

}
#scrol {
  transition-duration: 300ms;
  transform: translate3d(0px, 0px, 0px);
}
.container {
  .cccc::-webkit-scrollbar {
    display: none;
  }
  .row {
    .mainImgBox {
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;
      display: flex;
      background-color: #2569e6;
      .ScrolImgSec {
        padding: 1.5rem;
        img {
          height: 3rem;
          padding: 6px;
        }
        .imgContent {
          padding-top: 11rem;
          color: white;
          h6 {
            font-family: Poppins, sans-serif;
            font-size: 1rem;
            font-weight: 500;
          }
          p {
            padding-top: 1rem;
            font-family: Poppins, sans-serif;
          }
        }
      }

      .srcollSieImg {
        overflow: hidden;
        img {
          max-width: 300px;
        }
      }
    }
  }
}
// Client Section
@keyframes slide {
  0% {
    transform: translate3d(0,0,0);
  }
  
  100% {
    transform: translate3d(-1692px,0,0);
  }
}
// Footer 

ul {
    margin: 0px;
    padding: 0px;
}
.footer-section {
  background:white;
  position: relative;
}
.footer-cta {
  border-bottom: 1px solid #e6e6e6;
}
.single-cta i {
  color: #ff5e14;
  font-size: 30px;
  float: left;
  margin-top: 8px;
}
.cta-text {
  padding-left: 15px;
  display: inline-block;
}
.cta-text h4 {
  color: #1b1b1b;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 5px;
}
.cta-text span {
  color: #757575;
  font-size: 15px;
}
.footer-content {
  position: relative;
  z-index: 2;
}
.footer-pattern img {
  position: absolute;
  top: 0;
  left: 0;
  height: 330px;
  background-size: cover;
  background-position: 100% 100%;
}
.footer-logo {
  margin-bottom: 30px;
}
.footer-logo img {
    max-width: 200px;
}
.footer-text p {
  margin-bottom: 14px;
  font-size: 14px;
      color: #7e7e7e;
  line-height: 28px;
}
.footer-social-icon span {
  color: #fff;
  display: block;
  font-size: 20px;
  font-weight: 700;
  font-family: 'Poppins', sans-serif;
  margin-bottom: 20px;
}
.footer-social-icon a {
  color: #fff;
  font-size: 16px;
  margin-right: 15px;
}
.footer-social-icon i {
  height: 40px;
  width: 40px;
  text-align: center;
  line-height: 38px;
  border-radius: 50%;
}
.facebook-bg{
  background: #3B5998;
}
.twitter-bg{
  background: #55ACEE;
}
.google-bg{
  background: #DD4B39;
}
.footer-widget-heading h3 {
  color: #1b1b1b;
  font-weight: 500;
  font-size: 16px;
 font-family: Poppins, sans-serif;
  margin-bottom: 20px;

  margin-top: 1rem;
  position: relative;
}
.footer-widget ul {
display: flex;
flex-direction: column;
}
.footer-widget ul li {
  display: inline-block;
  margin-bottom: 12px;
}
.footer-widget ul li a:hover{
  color: #55ACEE
}
.footer-widget ul li a {
  color: #6a6a6a;
  text-transform: capitalize;
  text-decoration: none;
  font-size: 14px;
}
.subscribe-form {
  position: relative;
  overflow: hidden;
}
.subscribe-form input {
  width: 100%;
  padding: 14px 28px;
  background: #2E2E2E;
  border: 1px solid #2E2E2E;
  color: #fff;
}
.subscribe-form button {
    position: absolute;
    right: 0;
    background: #ff5e14;
    padding: 13px 20px;
    border: 1px solid #ff5e14;
    top: 0;
}
.subscribe-form button i {
  color: #fff;
  font-size: 22px;
  transform: rotate(-6deg);
}
.copyright-area{
  background: white;
  border-top: 1px solid #e6e6e6;
  padding: 25px 0;
}
.copyright-text p {
  margin: 0;
  font-size: 14px;
  color: #878787;
}
.copyright-text p a{
  color: #0755e9;
}
.footer-menu li {
  display: inline-block;
  margin-left: 20px;
}
.footer-menu li:hover a{
  color: #2569e6;
}
.footer-menu li a {
  font-size: 14px;
  color: #878787;
}
.client {
  background: url(https://tkxel.com/wp-content/themes/tkxel/assets/images/clients/clients-slide.svg)
    repeat-x;
  height: 150px;
  width: 20076px;
  animation: 120s linear infinite slide;
}
@media screen and (max-width: 1412px) {
  .nav-btn {
    display: none;
  }
}
@media screen and (max-width: 996px) {
  .rsponsive-nav {
    display: block;
  }
  .nav-first {
    display: none;
  }
  .pImg-1 {
    width: 90% !important;
  }
  .pImg-2 {
    margin-top: 2rem !important;
    width: 90% !important;
    margin: auto;
  }
}
@media screen and (max-width: 578px) {
  .hero {
    padding-top: 4rem;
  }
  .hero .hero-section h1 {
    font-size: 40px;
    line-height: 63px;
  }
  .hero .heroSiblingSection .col-sm-4 {
    text-align: center;
    margin-bottom: 4rem;
  }
  .hero .heroSiblingSection {
    margin-top: 4rem;
  }
  .hero .hero-image {
    margin-top: 3rem;
    margin-bottom: 4rem;
  }
  .firstSection h2 {
    font-size: 45px;
  }
}

@media screen and (max-width: 300px) {
  .firstSection button {
    padding: 3px 56px;
  }
  .firstSection h2 {
    font-size: 30px;
    line-height: 46px;
  }
}
